import React from "react";
import classes from './Post.module.css'


const Post = (props) => {

    const onClickLike = () => {

    }

    return (
        <div className={classes.user}>
            {console.log(props)}
            {props.myPosts.map( (item) => {
          return (
              <p key={item.id}>
                  <img className={classes.avatar} src='data:image/jpeg;base64,' alt='avatar'/>
                  {item.message} <br/>
                  <i>Like {item.like}</i>
                  <button onClick={onClickLike}>Like</button>

              </p>
        )})}
        </div>

    )
}


export default Post
